.sign-four {
  animation: sign-four 1s 0s ease infinite;
  -moz-animation: sign-four 1s 0s ease infinite;
  /* Firefox */
  -webkit-animation: sign-four 1s 0s ease infinite;
  /* Safari and Chrome */
  -o-animation: sign-four 1s 0s ease infinite;
  /* Opera */
}

@-webkit-keyframes sign-four {
  0%,
  100% {
    text-shadow: -0.1vw 0vw 0.1vw rgb(254, 222, 40), -0.15vw 0vw 0.2vw #fed128,
      -0.2vw 0vw 0.2vw #fed128, -0.1vw 0vw 3vw #f0130b, -0.2vw 0vw 3vw #f0130b,
      -0.4vw 0vw 3vw #f0130b, -0.1vw 0vw 5vw #f0130b, -0.2vw 0vw 5vw #f0130b,
      -0.4vw 0vw 0.8vw #f0130b, 0.2vw 0vw 10vw #f0130b;
    color: rgb(254, 40, 254);
  }

  50% {
    text-shadow: -0.1vw 0vw 0.1vw #705c12, -0.15vw 0vw 0.2vw #705c12,
      -0.2vw 0vw 0.2vw #705c12, -0.1vw 0vw 0.1vw #5c0704,
      -0.2vw 0vw 0.1vw #5c0704, -0.4vw 0vw 0.1vw #5c0704,
      -0.1vw 0vw 0.2vw #5c0704, -0.2vw 0vw 0.2vw #5c0704,
      -0.4vw 0vw 0.2vw #5c0704, 0.2vw 0vw 0.5vw #5c0704;
    color: #705c12;
  }
}

@keyframes sign-four {
  0%,
  100% {
    text-shadow: -0.1vw 0vw 0.1vw rgb(254, 222, 40), -0.15vw 0vw 0.2vw #fed128,
      -0.2vw 0vw 0.2vw #fed128, -0.1vw 0vw 3vw #f0130b, -0.2vw 0vw 3vw #f0130b,
      -0.4vw 0vw 3vw #f0130b, -0.1vw 0vw 5vw #f0130b, -0.2vw 0vw 5vw #f0130b,
      -0.4vw 0vw 0.8vw #f0130b, 0.2vw 0vw 10vw #f0130b;
    color: rgb(254, 40, 254);
  }

  50% {
    text-shadow: -0.1vw 0vw 0.1vw #705c12, -0.15vw 0vw 0.2vw #705c12,
      -0.2vw 0vw 0.2vw #705c12, -0.1vw 0vw 0.1vw #5c0704,
      -0.2vw 0vw 0.1vw #5c0704, -0.4vw 0vw 0.1vw #5c0704,
      -0.1vw 0vw 0.2vw #5c0704, -0.2vw 0vw 0.2vw #5c0704,
      -0.4vw 0vw 0.2vw #5c0704, 0.2vw 0vw 0.5vw #5c0704;
    color: #705c12;
  }
}

.Calculator {
  background-color: rgb(32, 32, 32);
  color: white;
  margin-top: 20px;
}

.inputs {
  flex-direction: column;
  width: 90%;
  margin: auto;
}

.input-field label {
  color: rgb(255, 255, 255);
}

.input-field input[type=text]:focus+label {
  color: #F20B70;
}

.input-field input[type=text]:focus {
  border-bottom: 1px solid #F20B70;
  box-shadow: 0 1px 0 0 #F20B70;
}

.input-field input[type=number]:focus+label {
  color: #F20B70;
}

.input-field input[type=number]:focus {
  border-bottom: 1px solid #F20B70;
  box-shadow: 0 1px 0 0 #F20B70;
}

